<template>
  <div>
    <div class="p-3">
      <div  class="p-2 shadow  rounded shadow" style="background:#5495ff;padding:20px;box-shadow:1px 1px 2px 1px grey;border-radius:20px;">
        <div v-if="recognition">
          <p class="p-2 font-semibold text-lg animate__animated animate__bounce animate__infinite">Recognition ...</p>
        </div>
        <div v-else> 
          <div v-if="dbFace">DB <span  style="font-weight:bold;">Ready </span> ({{dbTime.substring(0,15)}}) <button type="button" @click="saveDatabase"  style="background:#ffc107;margin-left:20px;border-radius:10px;border:1.5px solid grey;padding:3px;position:absolute;right:10px;">Update DB</button></div>
          <div v-else>Loading Getting Database ... </div>
          <p class="text-lg">Face Recognition :
            <span v-if="loading">Loading ...</span>
            <span v-else class="font-semibold">Ready</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import * as faceapi from "@vladmandic/face-api";
var port;
import axios from 'axios';
export default {
  data() {
    return {
      img:null,
      recognition:false,
      loading:true,
      dbFace:null,
      dbTime:''
    };
  },
  methods: {
    test(){
        port.postMessage('test');
    },  
    async saveDatabase(){
      this.dbFace=false;
      this.dbTime=false;
      let data = await axios.get('https://hitfaceapi.my.id/contact');
      let datas=data.data.datas
      localStorage.setItem('db-face',JSON.stringify(datas));
      localStorage.setItem('db-time',new Date());
      this.loadDatabase();
    },
    loadDatabase(){
      if(localStorage.getItem('db-face')){
        this.dbFace=localStorage.getItem('db-face');
      }
      if(localStorage.getItem('db-time')){
        this.dbTime=localStorage.getItem('db-time');
        console.log(this.dbTime)
      }
      this.$forceUpdate();
    },
    async startScan(videoEl) {
      let that = this;
      that.recognition=false;
      var maxDescriptorDistance = 0.6;
      // var img = document.getElementById("image1");
      var img = new Image();
      img.src=this.img;
      // videoEl = video;
      // var canvas = faceapi.createCanvasFromMedia(video);
      // canvas.style.position = "absolute;";
        // taruh base64 ke canvas
        // const img = await canvas.loadImage(this.img);
        // mendapatkan descriptor dari canvas
        // let descriptorImg = await faceapi.detectSingleFace(img).withFaceLandmarks().withFaceDescriptor();
        let descriptorImg = await faceapi.detectSingleFace(img,new faceapi.TinyFaceDetectorOptions({ inputSize: 320 })).withFaceLandmarks(true).withFaceDescriptor()

        let tensor;
        let wajah;
        if (!descriptorImg) {
            wajah = false;
        } else {
            const regionsToExtract = [
                new faceapi.Rect(descriptorImg.detection.box.x, descriptorImg.detection.box.y, descriptorImg.detection.box.width, descriptorImg.detection.box.height)
            ]
            let faceImages = await faceapi.extractFaces(img, regionsToExtract)
            if (faceImages.length == 0) {
                wajah = false;
            } else {
                faceImages.forEach(cnv => {
                    wajah = cnv.toDataURL();
                })
            }
            // mengconvert data face descriptor ke type Float32Array
            tensor = [
                new Float32Array(descriptorImg.descriptor)
            ]
        }

        if (wajah) {
            let datas = JSON.parse(localStorage.getItem('db-face'));
            // let data = await axios.get('./data-wajah.json');
            // let datas=data.data
            // let datas = await readJsonWajah();
            // console.log(datas)
            if (datas.length > 0) {
                // CEK DUPLICATE
                const labeledFaceDescriptors = await Promise.all(
                    datas.map(async label => {
                        let faces = JSON.parse(label.tensor)
                        if (faces) {
                            if (faces.length > 0) {
                                const faceDescriptors = []
                                faces.forEach(e => {
                                    // console.log(Object.values(e))
                                    faceDescriptors.push(new Float32Array(Object.values(e))) // multiple tensor data
                                })
                                // console.log(faceDescriptors)
                                return new faceapi.LabeledFaceDescriptors(JSON.stringify({
                                    id_user: label.id_user
                                }), faceDescriptors)
                            }
                        }
                    })
                )
                
                const faceMatcher = new faceapi.FaceMatcher(labeledFaceDescriptors, maxDescriptorDistance)
                if (descriptorImg) {
                    const results = faceMatcher.findBestMatch(descriptorImg.descriptor)
                    if (results._label == "unknown") {
                        if (tensor[0]) {
                            that.recognition=false;
                            port.postMessage(JSON.stringify({
                                message: "wajah tidak dikenali",
                                status: false,
                                confidence: 100 - (results._distance * 100)
                            }));
                            return;
                        }
                    } else {
                        let user = JSON.parse(results.label);
                        that.recognition=false;
                        port.postMessage(JSON.stringify({
                            message: "Wajah Mirip dengan id user = " + user.id_user,
                            status: true,
                            duplicate:true,
                            confidence: 100 - (results._distance * 100),
                            id_user: user.id_user
                        }));
                        console.log(JSON.stringify({
                            // tensor: tensor,
                            // wajah: wajah,
                            message: "Wajah Mirip dengan id user = " + user.id_user,
                            status: true,
                            duplicate:true,
                            confidence: 100 - (results._distance * 100),
                            id_user: user.id_user
                        }));
                        return;
                    }
                }
            } else {
                port.postMessage('false');
                that.recognition=false;
                port.postMessage(JSON.stringify({
                    message: 'Tidak ada wajah di Database ',
                    status: false
                }));
                return;
            }
        } else {
            that.recognition=false;
            port.postMessage('false');
            port.postMessage(JSON.stringify({
                message: 'Wajah tidak ditemukan',
                status: false
            }));
            return;
        }
    },
    async loadModel() {
      var MODEL_URL = "./model";
      await faceapi.loadTinyFaceDetectorModel(MODEL_URL);
      await faceapi.loadFaceRecognitionModel(MODEL_URL);
      await faceapi.loadFaceLandmarkTinyModel(MODEL_URL);
      //   await faceapi.loadFaceExpressionModel(MODEL_URL);
      console.log("loading model berhasil ...");
      try{
        port.postMessage('loading');
        this.loading=false;
      }
      catch(err){
        this.loading=false;
      }
    },
  },
  mounted() {
    let that=this
    if(!localStorage.getItem('db-face')){
      this.saveDatabase();
    }
    this.loadDatabase();
    window.addEventListener('message', function(event) {
        if (event.data == 'capturePort') {
            if (event.ports[0] != null) {
                port = event.ports[0];
                port.onmessage = function (event) {
                    console.log(event.data);
                    that.img=event.data;
                    that.startScan();
                };
            }
        }
    }, false);
    this.loadModel();
    
  },
};
</script>
<style>
body{
  margin:0;
  padding:0;
}
</style>